import style from './style.module.css';

const DecoratedText = (props) =>{
    return (
        <div className = {style.wrapper}>
            <h2 className = {style.textLeft}>
                {props.text}
            </h2>
            <h2 className = {style.textCenter}>
                {props.text}
            </h2>
            <h2 className = {style.textRigth}>
                {props.text}
            </h2>
        </div>
    );
}

export default DecoratedText;